﻿@import (reference) "./_colors-branding.less";

// Base

@x-clr-bs-lt: "sixty__@{branding-secondary}--base";
@x-clr-bs-dt: "fifty__@{branding-secondary}--base";

@sixty__red--base        : 0 65% 48%;
@fifty__red--base        : 0 78% 62%;
@sixty__pink--base       : 340 66% 47%;
@fifty__pink--base       : 340 72% 61%;
@sixty__purple--base     : 293 52% 48%;
@fifty__purple--base     : 293 61% 63%;
@sixty__deeppurple--base : 265 57% 55%;
@fifty__deeppurple--base : 265 73% 70%;
@sixty__indigo--base     : 230 75% 58%;
@fifty__indigo--base     : 230 90% 66%;
@sixty__blue--base       : 212 76% 46%;
@fifty__blue--base       : 212 72% 65%;
@sixty__lightblue--base  : 199 72% 54%;
@fifty__lightblue--base  : 199 71% 66%;
@sixty__cyan--base       : 185 74% 45%;
@fifty__cyan--base       : 185 68% 59%;
@sixty__teal--base       : 162 74% 43%;
@fifty__teal--base       : 162 62% 56%;
@sixty__green--base      : 120 57% 47%;
@fifty__green--base      : 120 54% 59%;
@sixty__lightgreen--base : 90 68% 45%;
@fifty__lightgreen--base : 90 64% 59%;
@sixty__lime--base       : 72 77% 47%;
@fifty__lime--base       : 72 74% 59%;
@sixty__yellow--base     : 53 90% 49%;
@fifty__yellow--base     : 53 90% 60%;
@sixty__amber--base      : 42 92% 49%;
@fifty__amber--base      : 42 95% 60%;
@sixty__orange--base     : 28 92% 44%;
@fifty__orange--base     : 28 90% 55%;
@sixty__deeporange--base : 14 70% 52%;
@fifty__deeporange--base : 14 70% 55%;
@sixty__brown--base      : 13 30% 38%;
@fifty__brown--base      : 13 25% 55%;
@sixty__grey--base       : 0 0% 25%;
@fifty__grey--base       : 0 0% 75%;

// Tints and shades

@tint-10 : 92%;
@tint-20 : 80%;
@tint-30 : 60%;
@tint-40 : 30%;

@shade-70 : 30%;
@shade-80 : 50%;
@shade-90 : 80%;
@shade-100: 92%;

// Desaturates

@desat-10 : 35%;
@desat-20 : 30%;
@desat-30 : 25%;
@desat-40 : 20%;
@desat-70 : 20%;
@desat-80 : 25%;
@desat-90 : 45%;
@desat-100: 50%;

// Opacity

@opacity-1 : 10%;
@opacity-2 : 20%;
@opacity-3 : 40%;
@opacity-4 : 60%;
@opacity-5 : 80%;

// Generated pallete

// Orange

@hex-orange--10  :  desaturate(tint(hsl(extract(@fifty__orange--base,1), extract(@fifty__orange--base,2), extract(@fifty__orange--base,3)), @tint-10), @desat-10);
