@hex-orange--100 : desaturate(shade(hsl(extract(@sixty__orange--base,1), extract(@sixty__orange--base,2), extract(@sixty__orange--base,3)), @shade-100), @desat-100);

@orange--10  :  hsl(round(hue(@hex-orange--10)) round(saturation(@hex-orange--10)) round(lightness(@hex-orange--10)));
@orange--20  :  hsl(round(hue(@hex-orange--20)) round(saturation(@hex-orange--20)) round(lightness(@hex-orange--20)));
@orange--30  :  hsl(round(hue(@hex-orange--30)) round(saturation(@hex-orange--30)) round(lightness(@hex-orange--30)));
