@orange--70  :  hsl(round(hue(@hex-orange--70)) round(saturation(@hex-orange--70)) round(lightness(@hex-orange--70)));
@orange--80  :  hsl(round(hue(@hex-orange--80)) round(saturation(@hex-orange--80)) round(lightness(@hex-orange--80)));
@orange--90  :  hsl(round(hue(@hex-orange--90)) round(saturation(@hex-orange--90)) round(lightness(@hex-orange--90)));
@orange--100 :  hsl(round(hue(@hex-orange--100)) round(saturation(@hex-orange--100)) round(lightness(@hex-orange--100)));


